.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.intro{
    max-width: 540px;
    text-align: center;
    z-index: 1;
}
.home_img{
    width: 250px;
    border-radius: 10px;
    margin-bottom: 1rem;
    margin-top: 0.5rem;
}
.home_name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}
.home_socials{
    display: flex;
    column-gap: 1rem;
    margin: 0.5rem;
    align-items: center;
    justify-content: center;
}
.home_socials i:hover{
    transform: scale(1.5);
}
.home_education{
    font-size: 20px;
    font-weight: 600;
}
.home-role-tagline{
    font-size: 18px;
    margin: 5px 0 0 0;
}
.home_social-link{
    color: var(--title-color);
    font-size: 1rem;
    transition: .3s;
}
.home_social-link:hover{
    color: hsl(43, 100%, 68%);
}
.scroll_down{
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}
.btn{
    margin-bottom: 4.5rem;
}
.home_scroll-name{
    font-size: var(--small-font-size);
}
.mouse{
    border: 2px solid var(--text-color);
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    border-radius: 1rem;
    position: relative;
}
@keyframes ani-mouse{
    0%{
        top: 29%;
    }
    15%{
        top: 50%;
    }
    50%{
        top: 50%;
    }
    100%{
        top: 29%;
    }
}
.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}
.shapes{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}
.shape{
    position: absolute;
}
.s1{
    left: 2%;
    top: 10%;
}
.s2{
    left: 18%;
    top: 30%;
}
.s3{
    left: 5%;
    bottom: 30%;
}
.s4{
    left: 2%;
    bottom: 10%;
}
.s5{
    left: 44%;
    top: 5%;
}
.s6{
    left: 36%;
    bottom: 10%;
}
.s7{
    top: 20%;
    right: 25%;
}
.s8{
    right: 24%;
    bottom: 20%;
}
.s9{
    right: 2%;
    top: 10%;
}
.s10{
    top: 45%;
    right: 11%;
}
.s11{
    bottom: 10%;
    right: 2%;
}
@media screen and (max-width: 1024px) {
    .home_social-link{
      font-size: 1.125rem;
    }
  }